/**
 *
 * @param type
 * @param description
 * @description: prepare the event so that it may be identical with the interface
 */
export const eventDefiner = (event: eventDefinerInterface) =>
  ({
    event_id: "uuid",
    user_id: event.user_id,
    event_type: event.event_type,
    description: event.description,
    room_id: event.room_id,
    done_by: event.done_by,
    time: new Date().toDateString(),
  } as EventInterface);

/**
 * @description Get the room of the candidates
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_GetProctoringRoom = (
  {
    room_id,
    course_id,
    position_id,
  }: { room_id: string; course_id: string; position_id: string },
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      const res = await axios.get<CandidateProctoringInterfaceAPI[]>(
        `${API.APPLICATION}/candidatereadyforexam/${course_id}/${position_id}/${room_id}`
      );

      console.log({ res_proctoring_data: res.data });

      dispatch<AddCandidateProctoring>({
        type: ActionTypes.ADD_CANDIDATES_ROOM_PROCTORING,
        payload: res.data,
      });

      // console.log({ user_details: res.data });
      callback(true, [], "");
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, [], errorToText(error));
    }
  };
};

