interface GetRoomDetailsAPI extends RoomServer {
  room_exams: ProctoringRoomExam[];
}

/**
 * @description Get the room of the candidates
 * @param room_id
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_RoomDetails = (room_id: string, callback: Function) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      const res = await axios.get<GetRoomDetailsAPI>(
        `${API.APPLICATION}/examsinroom/${room_id}`
      );
      console.log({ FC_RoomDetails: res });

      console.log({ room_id });

      // const candidates = res.data.map(
      //   (itm: any) =>
      //     ({
      //       active: false,
      //       email: itm.email,
      //       fname: itm?.user_info?.fname,
      //       lname: itm?.user_info?.lname,
      //       stopped: false,
      //       user_id: itm.user_id,
      //       muted: false,
      //       peer: "",
      //       peer_id: "",
      //       ref: null,
      //     } as CandidateProctoringInterface)
      // );

      let { room_exams, ...roomDetailsData } = res.data;

      dispatch<LoadRoomDetails>({
        type: ActionTypes.LOAD_ROOM_DETAILS,
        payload: {
          room: roomDetailsData,
          room_exams: room_exams,
        },
      });

      console.log({ data_res: res.data });

      callback(true, "");
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

/**
 * @description Get the room of the candidates
 * @param room_id
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_ExamsDetailsData = (room_id: string, callback: Function) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      const res = await axios.get<ExamsListDates[]>(
        `${API.QBank}/exam/period/${room_id}`
      );

      console.log({ FC_ExamsDetailsData: res });

      // console.log({ res_data_exams: res });

      dispatch<ProctoringGetExamsList>({
        type: ActionTypes.PROCTORING_GET_EXAMS_LIST,
        payload: res.data,
      });

      callback(true, "");
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

