import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { CandidateExam } from "../interfaces/exam";
import { QuestionLanguage } from "../interfaces/question";
import { PrepareExamStatus } from "../interfaces/prepare_exam";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface InitialExamDetails {
  period: string;
  course: string;
  exam_status: PrepareExamStatus;
  termes: string;
  exam_date: string;
  start_time: string;
  duration: number;
  discription: string;
  id: string;
}

export interface QuestionAnswersStore {
  question_id: string;
  status: boolean; //Check if submitted online
  answer: {
    id: string;
    value: any;
    status: any;
  }[];
}

export interface loadCandidateExamActions {
  type: ActionTypes.LOAD_CANDIDATE_EXAM,
  payload: CandidateExam
}

export interface setSelectedCandidateExamIdActions {
  type: ActionTypes.SELECTED_EXAM_ID,
  payload: string | null
}

export interface setQuestionLanguageActions {
  type: ActionTypes.SET_QUESTION_LANGUAGE,
  payload: QuestionLanguage
}

export interface setInitialExamDetailsActions {
  type: ActionTypes.SET_INITIAL_EXAM_DETAILS,
  payload: InitialExamDetails
}

export interface setSelectedCandidateExamQuestionActions {
  type: ActionTypes.SELECTED_EXAM_QUESTION,
  payload: {
    exam_id: string;
    question_id: string | null;
  }
}

export interface setExamQuestionAnswerActions {
  type: ActionTypes.SET_EXAM_QUESTION_ANSWER,
  payload: QuestionAnswersStore
}

export interface candidateExamStoreInterface {
  candidate_exam: CandidateExam | null;
  selected_exam_id: string | null;
  selected_question: string | null;
  selected_language: QuestionLanguage;
  question_answers: QuestionAnswersStore[];
  selected_exam_details: InitialExamDetails[]
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadCandidateExam = (exam_id: string, callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<CandidateExam>(`${API.QBank}/exam/${exam_id}`);
      dispatch<loadCandidateExamActions>({
        type: ActionTypes.LOAD_CANDIDATE_EXAM,
        payload: res.data,
      });
      console.log("Exam: ", res.data);
    } catch (error) {
      console.log("Err: ", error);
    }
    callback(false);
  };
};

export const setSelectedCandidateExam = (exam_id: string | null): setSelectedCandidateExamIdActions => {
  return {
    type: ActionTypes.SELECTED_EXAM_ID,
    payload: exam_id,
  };
}

export const setSelectedExamQuestion = (exam_id: string, question_id: string | null): setSelectedCandidateExamQuestionActions => {
  return {
    type: ActionTypes.SELECTED_EXAM_QUESTION,
    payload: {
      exam_id: exam_id,
      question_id: question_id
    }
  };
}

export const setQuestionLanguage = (lang: QuestionLanguage): setQuestionLanguageActions => {
  return {
    type: ActionTypes.SET_QUESTION_LANGUAGE,
    payload: lang,
  };
}

export const setExamQuestionAnswer = (answer: QuestionAnswersStore): setExamQuestionAnswerActions => {
  return {
    type: ActionTypes.SET_EXAM_QUESTION_ANSWER,
    payload: answer
  };
}

export const InitializeExamDetails = (periodId: string, courseId: string, callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<InitialExamDetails>(`${API.QBank}/exam/details/${periodId}/${courseId}`);
      dispatch<setInitialExamDetailsActions>({
        type: ActionTypes.SET_INITIAL_EXAM_DETAILS,
        payload: res.data,
      });
      console.log("Selected Exam: ", res.data);
    } catch (error) {
      console.log("Err: ", error);
    }
    callback(false);
  };
}