import React from "react";
import Chart from "react-apexcharts";

type VBarChartInterface = {
  // labels: string[];
  categoriesLabel: string[];
  bar?: {
    borderRadius: number;
    horizontal: boolean;
    columnWidth: string;
    endingShape: string;
  };
  dataLabels: boolean;
  yaxisText: string;
  tooltipText: string;
  series: {
    name: string;
    data: number[];
  }[];
  defaultBar?: {
    borderRadius: number;
    horizontal: boolean;
    columnWidth: string;
    endingShape: string;
  };
};

const defaultBar = {
  borderRadius: 4,
  horizontal: false,
  columnWidth: "95%",
  endingShape: "rounded",
};

const VBarChart: React.FC<VBarChartInterface> = (props) => {
  var options = {
    plotOptions: {
      bar: props.defaultBar ? props.defaultBar : defaultBar,
    },
    labels: props.categoriesLabel,
    dataLabels: {
      enabled: true,
    },
    xaxis: {
      categories: props.categoriesLabel,
    },
    yaxis: {
      title: {
        text: props.yaxisText,
      },
    },
    fill: {
      opacity: 1,
    },
    tooltip: {
      y: {
        formatter: function (val: number) {
          return val + " " + props.tooltipText;
        },
      },
    },
  };
  return (
    <div>
      <Chart
        options={options}
        series={props.series}
        type="bar"
        width="100%"
        height="450"
      />
    </div>
  );
};

export default VBarChart;
