import { Action, ActionTypes, candidateExamStoreInterface } from "../actions";
import { QuestionLanguage } from "../interfaces/question";

// default state
const defaultState: candidateExamStoreInterface = {
  candidate_exam: null,
  selected_exam_id: null,
  selected_question: null,
  selected_language: QuestionLanguage.Eng,
  question_answers: [],
  selected_exam_details: [],
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const candidateExamReducer = (state: candidateExamStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOAD_CANDIDATE_EXAM:
      return {...state, 
        candidate_exam: action.payload,
        selected_question: action.payload.questions[0].id
      };
    case ActionTypes.SELECTED_EXAM_ID:
      return {...state, selected_exam_id: action.payload};
    case ActionTypes.SELECTED_EXAM_QUESTION:
      return {...state, selected_question: action.payload.exam_id === state.selected_exam_id ? action.payload.question_id : null};
    case ActionTypes.SET_QUESTION_LANGUAGE:
      return {...state, selected_language: action.payload};
    case ActionTypes.SET_INITIAL_EXAM_DETAILS:
      return {
        ...state, 
        selected_exam_details: state.selected_exam_details.find(itm => itm.id === action.payload.id) ? state.selected_exam_details : [...state.selected_exam_details, action.payload],
      };
    case ActionTypes.SET_EXAM_QUESTION_ANSWER:
      return {
        ...state, 
        question_answers: state.question_answers.filter(itm => itm.question_id === action.payload.question_id).length === 0 ? 
        [...state.question_answers, action.payload] : [...state.question_answers.filter(itm1 => itm1.question_id !== action.payload.question_id), action.payload]
      };
    default:
      return state;
  }
};