import React, { Fragment, useEffect, useState } from "react";
import { AiFillDashboard } from "react-icons/ai";
import {
  MainItem,
  SideNavigation,
} from "../../components/Navigation/SideNavigation";
import HBarChart from "./ChartComponents/HBarChart";
import VBarChart from "./ChartComponents/VBarChart";
import PieChart from "./ChartComponents/PieChart";
import DashboardLabels from "./DashboardLabels";
import { StoreState } from "../../reducers";
import { connect } from "react-redux";
import { getAllRoom } from "../../actions/location";
import { PopupAlertData } from "../../components/Alert/Popup";
import {
  Auth,
  GetApplicationPerPeriod,
  GetUserInfo,
  GetUserInfoInterface,
  loadEducationCategories,
  PushPopupAlert,
} from "../../actions";
import {
  ApplicationPeriodInterface,
  ApplicationSessionInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
} from "../../shared/interfaces";
import { education_category } from "../../interfaces/education_category";
import { DashboardReducerType } from "../../reducers/dashboard";
import { errorToText } from "../../utils/errors";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI } from "../../interfaces/position";
import { getApplicationSessions, getPositions } from "../../actions/settings";
import { FaFilter } from "react-icons/fa";
import { ApplicationStates } from "../../interfaces/application";
import ApplicationsGender from "./ChartComponents/ApplicationsGender";
import { LocationsInArray } from "../UserManagement/UserList";

interface AppProps {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  application_sessions: ApplicationSessionInterface[];
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getAllRoom: Function;
  GetUserInfo: Function;
  GetApplicationPerPeriod: Function;
  getPositions: Function;
  getApplicationSessions: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Index: React.FC<AppProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingEducation, setLoadingEducation] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] =
    useState<boolean>(false);
  const [thisEducation, setThisEducation] = useState<education_category | null>(
    null
  );
  const [thisPeriod, setThisPeriod] = useState<string>("");
  const [thisAppStatus, setThisAppStatus] = useState<ApplicationStates>(
    ApplicationStates.Shortlisted
  );

  const positionApplications = {
    // labels: string[];
    categoriesLabel: [
      "District 1",
      "District 2",
      "District 2",
      "District 3",
      "District 4",
      "District 5",
      "District 6",
      "District 7",
      "District 8",
      "District 9",
      "District 10",
      "District 1",
      "District 2",
      "District 2",
      "District 3",
      "District 4",
      "District 5",
      "District 6",
      "District 7",
      "District 8",
      "District 9",
      "District 10",
      "District 1",
      "District 2",
      "District 2",
      "District 3",
      "District 4",
      "District 5",
      "District 6",
      "District 7",
      "District 8",
      "District 9",
      "District 10",
    ],
    dataLabels: false,
    yaxisText: "(Number of Applications)",
    tooltipText: "applications",
    series: [
      {
        name: "Math",
        data: [
          44, 55, 57, 56, 61, 58, 63, 60, 66, 50, 44, 55, 57, 56, 61, 58, 63,
          60, 66, 50, 44, 55, 57, 56, 61, 58, 63, 60, 66, 50,
        ],
      },
    ],
  };
  const [roomTables, setRoomTables] = useState<
    LocationRoomTableInterface[] | null
  >(null);
  const [distinctDistricts, setDistinctDistricts] = useState<
    LocationRoomTableInterface[] | null
  >(null);
  const [distinctDistrictNames, setDistinctDistrictNames] = useState<
    string[] | null
  >(null);

  useEffect(() => {
    if (props.education_categories === null) {
      props.loadEducationCategories(setLoadingEducation);
    } else if (props.dashboard.userInfo === null) {
      setLoadingEducation(true);
      props.GetUserInfo((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (!status && msg.length > 0) {
          props.PushPopupAlert({
            title: errorToText(msg),
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (props.systemPositions === null) {
      props.getPositions((status: boolean, msg: string) => {
        setLoadingEducation(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (!props.application_sessions.length) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (thisEducation === null) {
      if (
        props.dashboard.applications === null &&
        props.education_categories.length > 0
      ) {
        setLoadingEducation(true);
        props.GetApplicationPerPeriod(
          props.education_categories[0],
          (status: boolean, msg: string) => {
            setLoadingEducation(status);
            if (!status && msg.length > 0) {
              props.PushPopupAlert({
                title: errorToText(msg),
                theme: PopupAlertTheme.danger,
              });
            }
          }
        );
      }
      setThisEducation(props.education_categories[0]);
    } else if (!props.locations.length) {
      props.getAllRoom((status: boolean, msg: string) => {
        if (status === true && msg.length > 0) console.error(msg);
      });
    } else if (props.dashboard.applications === null) {
      setLoadingEducation(true);
      let active_period: ApplicationSessionInterface | undefined;
      if (!thisPeriod.length) {
        active_period = props.application_sessions.find(
          (item) => item.is_active
        );
        setThisPeriod(
          active_period
            ? active_period.application_session_id
            : props.application_sessions[0].application_session_id
        );
      }
      props.GetApplicationPerPeriod(
        active_period
          ? active_period.application_session_id
          : props.application_sessions[0].application_session_id,
        (status: boolean, msg: string) => {
          setLoadingEducation(status);
          if (!status && msg.length > 0) {
            props.PushPopupAlert({
              title: errorToText(msg),
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else {
      // LOAD DISTINCT DISTRICTS FROM TABLE LOCATION
      setRoomTables(LocationsInArray(props.locations));
      if (!distinctDistricts || !distinctDistricts.length) {
        var flags: string[] = [],
          names: string[] = [],
          output: LocationRoomTableInterface[] = [],
          l: number = LocationsInArray(props.locations).length,
          i: number;
        for (i = 0; i < l; i++) {
          if (flags.includes(LocationsInArray(props.locations)[i].district_id))
            continue;
          flags.push(LocationsInArray(props.locations)[i].district_id);
          names.push(LocationsInArray(props.locations)[i].district_name);
          output.push(LocationsInArray(props.locations)[i]);
        }
        setDistinctDistricts(output);
        setDistinctDistrictNames(names);
      }

      setLoading(false);
      setLoadingEducation(false);
    }
  }, [props, thisEducation, thisPeriod.length, distinctDistricts]);

  if (loading === true || loadingEducation === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading...
      </div>
    );
  }

  const getPositionsByInstitute = (): {
    data: PositionAPI[];
    ids: string[];
  } => {
    let ids: string[] = [],
      data: PositionAPI[] = [];
    if (props.systemPositions && thisEducation) {
      data = props.systemPositions.filter(
        (item) =>
          item.education_institute_id.education_institute_id ===
          thisEducation.education_institute_id
      );

      data.forEach((element) => {
        ids.push(element.position_id);
      });
    }
    return { data, ids };
  };

  const getApplicationsByInsitutes = (
    status: "ALL" | ApplicationStates = "ALL"
  ): {
    data: ApplicationPeriodInterface[];
    ids: string[];
    uids: string[];
  } => {
    let result: ApplicationPeriodInterface[] = [],
      ids: string[] = [],
      uids: string[] = [];
    if (props.dashboard.applications && thisEducation) {
      result = props.dashboard.applications.filter((item) =>
        getPositionsByInstitute().ids.includes(item.position)
      );

      let data: ApplicationPeriodInterface[] = [];
      if (status === ApplicationStates.Shortlisted) {
        data = result.filter(
          (item) => item.application_status === ApplicationStates.Shortlisted
        );
      } else if (status === ApplicationStates.Rejected) {
        data = result.filter(
          (item) => item.application_status === ApplicationStates.Rejected
        );
      } else if (status === "ALL") {
        data = result;
      }

      data.forEach((element) => {
        ids.push(element._id);
        if (!uids.includes(element.user_id)) uids.push(element.user_id);
      });
      return { data, ids, uids };
    }
    return { data: result, ids, uids };
  };

  const getAllApplicantByInsitute = (gender: "ALL" | "M" | "F" = "ALL") => {
    let ids: string[] = [],
      data: GetUserInfoInterface[] = [];
    if (props.dashboard.userInfo && thisEducation) {
      if (gender === "M") {
        data = props.dashboard.userInfo.filter(
          (item) =>
            getApplicationsByInsitutes().uids.includes(item.user_id) &&
            item.user_info.sex === "M"
        );
      } else if (gender === "F") {
        data = props.dashboard.userInfo.filter(
          (item) =>
            getApplicationsByInsitutes().uids.includes(item.user_id) &&
            item.user_info.sex === "F"
        );
      } else {
        data = props.dashboard.userInfo.filter((item) =>
          getApplicationsByInsitutes().uids.includes(item.user_id)
        );
      }
    }
    return { data, ids };
  };

  return (
    <Fragment>
      <SideNavigation>
        {/* <Search data={[1, 2, 3]} /> */}
        {/* EDUCATION CATEGORY sections */}
        <span className="text-sm text-gray-600 font-semibold">
          Education Institute
        </span>
        {props.education_categories &&
          props.education_categories.length > 0 &&
          props.education_categories.map((education, index) => (
            <section
              key={education.education_institute_id + index}
              className={`mt-2 ${
                (loading || loadingEducation || loadingApplications) &&
                "cursor-not-allowed"
              }`}
              onClick={() =>
                !loading &&
                !loadingEducation &&
                !loadingApplications &&
                setThisEducation(education)
              }
            >
              <MainItem
                title={education.education_category_title}
                active={
                  thisEducation
                    ? education.education_institute_id ===
                      thisEducation.education_institute_id
                    : false
                }
              />
            </section>
          ))}
        {/* end EDUCATION CATEGORY sections */}

        {/* APPLICATIONS STATUS sections */}
        <span className="text-sm text-gray-600 font-semibold">
          Application status
        </span>
        <section
          className={`mt-2 ${
            (loading || loadingEducation || loadingApplications) &&
            "cursor-not-allowed"
          }`}
          onClick={() =>
            !loading &&
            !loadingEducation &&
            !loadingApplications &&
            setThisAppStatus(ApplicationStates.Shortlisted)
          }
        >
          <MainItem
            title={"Shortlisted"}
            count={
              getApplicationsByInsitutes(ApplicationStates.Shortlisted).data
                .length
            }
            active={
              thisAppStatus === ApplicationStates.Shortlisted ? true : false
            }
          />
        </section>
        <section
          className={`mt-2 ${
            (loading || loadingEducation || loadingApplications) &&
            "cursor-not-allowed"
          }`}
          onClick={() =>
            !loading &&
            !loadingEducation &&
            !loadingApplications &&
            setThisAppStatus(ApplicationStates.Rejected)
          }
        >
          <MainItem
            title={"Rejected"}
            count={
              getApplicationsByInsitutes(ApplicationStates.Rejected).data.length
            }
            active={thisAppStatus === ApplicationStates.Rejected ? true : false}
          />
        </section>
        {/* END APPLICATIONS STATUS sections */}

        {/* <span className="text-sm text-gray-600 font-semibold">
          Application status
        </span>
        <section className="mt-2">
          <MainItem title={"Seats"} count={5} active={false} />
        </section> */}
      </SideNavigation>

      {/* HEADER TITLE */}
      <div className="mt-3 rounded flex flex-row justify-between items-center">
        <div className="flex flex-row items-center gap-3">
          <AiFillDashboard className="text-6xl text-blue-600" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-3xl font-bold">
              Application Dashboard
            </span>
            <span className="text-gray-600 text-sm">
              Customization of question bank predefined settings including
              courses, chapters, question types, etc
            </span>
          </div>
        </div>
        <div>
          <select
            className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none ${
              loadingApplications && "cursor-wait"
            }`}
            value={thisPeriod}
            disabled={loading || loadingApplications || loadingEducation}
            onChange={(e) => {
              setThisPeriod(e.target.value);
              setLoadingApplications(true);
              props.GetApplicationPerPeriod(
                e.target.value,
                (status: boolean, msg: string) => {
                  setLoadingApplications(status);
                  if (!status && msg.length > 0) {
                    props.PushPopupAlert({
                      title: errorToText(msg),
                      theme: PopupAlertTheme.danger,
                    });
                  }
                }
              );
            }}
          >
            {props.application_sessions.map((session, item) => (
              <option
                key={session.application_session_id + item}
                value={session.application_session_id}
              >
                {session.application_session_name}
              </option>
            ))}
          </select>
        </div>
      </div>
      {/* END HEADER TITLE */}

      <DashboardLabels
        applicants={getAllApplicantByInsitute().data.length}
        applications={getApplicationsByInsitutes().data.length}
        shortlisted={
          getApplicationsByInsitutes(ApplicationStates.Shortlisted).data.length
        }
        rejected={
          getApplicationsByInsitutes(ApplicationStates.Rejected).data.length
        }
      />

      {/* CHARTS SECTIONS GENDER && AGE ======================================= */}
      <section className="grid grid-cols-1 md:grid-cols-3 gap-2 my-3">
        {/* APPLICANTS BY GENDER ========================================= */}
        <div className="bg-white rounded col-span-1 md:col-span-1">
          <div className="px-3 py-4">
            <span className="text-gray-600 font-semibold text-lg">
              Applicants Gender
            </span>
          </div>
          <PieChart
            options={{ labels: ["Woman", "Man"] }}
            series={[
              getAllApplicantByInsitute("F").data.length,
              getAllApplicantByInsitute("M").data.length,
            ]}
          />
        </div>
        <div className="bg-white rounded col-span-1 md:col-span-2">
          <div className="flex items-center justify-between px-3">
            <span className="text-gray-600 font-semibold text-lg">
              Applicants Age
            </span>
            <div className="bg-white">
              <nav className="flex flex-col sm:flex-row">
                <button className="text-gray-600 py-4 px-6 block hover:text-blue-500 focus:outline-none border-b-2 font-medium border-blue-500">
                  All
                </button>
                <button className="text-blue-500 font-medium py-4 px-6 block hover:text-blue-500 focus:outline-none">
                  Woman
                </button>
                <button className="text-blue-500 font-medium py-4 px-6 block hover:text-blue-500 focus:outline-none">
                  Man
                </button>
              </nav>
            </div>
          </div>
          <HBarChart getAllApplicantByInsitute={getAllApplicantByInsitute} />
        </div>
        {/* END APPLICANTS BY GENDER ===================================== */}
      </section>
      {/* END CHARTS SECTIONS GENDER && AGE ======================================= */}

      {/* CHARTS SECTIONS NUMBER OF APPLICATION - GENDER ==================================== */}
      {distinctDistrictNames && (
        <ApplicationsGender
          getApplicationsByInsitutes={getApplicationsByInsitutes}
          districtNames={distinctDistrictNames}
        />
      )}
      {/* END CHARTS SECTIONS NUMBER OF APPLICATION - GENDER ================================ */}

      {/* CHARTS SECTIONS NUMBER OF APPLICATION - POSITION ==================================== */}
      <section className="my-3 bg-white">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 font-semibold text-lg">
            Number of Applications - Position
          </span>
          <div className="bg-white">
            <button className="flex items-center gap-2 text-gray-600 py-4 px-6 hover:text-blue-500 focus:outline-none border-b-2 font-medium border-blue-500">
              <FaFilter className="w-4 h-4" /> Teaching Mathematics
            </button>
            <nav className="flex flex-col sm:flex-row"></nav>
          </div>
        </div>
        <VBarChart
          categoriesLabel={positionApplications.categoriesLabel}
          dataLabels={positionApplications.dataLabels}
          yaxisText={positionApplications.yaxisText}
          tooltipText={positionApplications.tooltipText}
          series={positionApplications.series}
        />
      </section>
      {/* END CHARTS SECTIONS NUMBER OF APPLICATION - POSITION ================================ */}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  locations,
  schoolLevels,
  dashboard,
  system_settings,
}: StoreState): {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  application_sessions: ApplicationSessionInterface[];
} => {
  return {
    auth: auth,
    locations: locations.roomLocation,
    education_categories: schoolLevels.education_categories,
    dashboard,
    systemPositions: system_settings.systemPositions,
    application_sessions: system_settings.applicationSessions,
  };
};

export default connect(mapStateToProps, {
  getAllRoom,
  loadEducationCategories,
  getPositions,
  GetUserInfo,
  GetApplicationPerPeriod,
  getApplicationSessions,
  PushPopupAlert,
})(Index);
